
.footer {
    &_top {
        padding-bottom: 12px;
        border-bottom: 1px solid #8f96a1;
        
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        margin-bottom: 30px;
        @media (max-width:860px) {
            border-bottom: none;
            flex-direction: column;
            text-align: center;
        }
        @media (max-width:650px) {
            border-bottom: 1px solid #8f96a1;
            border-top: 1px solid #8f96a1;
        }
    }
    &_nav {
        
        font-size: 16px;
        font-family: $obold;
        @media (max-width:860px) and (min-width:650px) {
            margin-bottom: 24px;
            padding-bottom: 24px;
            border-bottom: 1px solid #8f96a1;
            width: 100%;
        }
        @media (max-width:650px) {
            margin-bottom: 24px; 
            padding-top: 20px;
        }
        a {
            color: #27302d;
            text-decoration: none;
            margin-right: 30px;
            @media (max-width:860px) {
                margin-left: 30px;   
            }
            @media (max-width:650px) {
                display: block;
                margin: 0 10px;
                text-align: center;
            }
            
            &:hover {
                color: $green;
            }
        }
    }
}
.social {
    a {
        color: #8f96a1;
        font-size: 40px;
        margin-left: 10px;
        text-decoration: none;
        &:hover {
            color: $green;
        }
        @media (max-width:650px) {
            margin: 0 5px;   
        }
    }
}
.copy {
    color: #8f96a1;
    font-size: 16px;
    padding-bottom: 30px;
    @media (max-width:860px) {
        text-align: center;   
    }
}
