header {
    padding-top: 28px;
    padding-bottom: 60px;
    @media (max-width:700px) {
        padding-bottom: 20px;   
    }
    .wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    min-width: 356px;
    
    
    a {
        white-space: nowrap;
        font-size: 16px;
        color: #27302d;
        font-family: $obold;
        text-decoration: none;
        i {
            margin-left: 8px;
            color: $green;
        }
    }
    @media (max-width:680px) {
        position: fixed;
        left: -320px;
        top: 0;
        bottom: 0;
        width: 270px;
        min-width: 270px;
        display: block;
        padding: 20px;
        border-right: 2px solid $green;
        background: #fff;
        box-sizing: border-box;
        transition: left 0.4s ease;
        z-index: 50;
        .nav_dd_block {
            display: block;
            position: relative;
            box-shadow: none;
            a {
                font-size: 14px;
                padding-left: 10px;
                margin-top: 7px;
            }
        }
        i {
            display: none;
        }
    }
}
.nav {
    &_dd {
        position: relative;
        &_block {
            position: absolute;
            top: 99%;
            display: none; 
        }
        & > a {
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }
        a {
            display: block;
            &:hover {
                color: $green;   
            }
        }
        &:hover {
            & > a {
                color: $green;
            }
            .nav_dd_block {
                display: block;
                padding: 15px;
                margin-left: -15px;
            }
        }
    }
    &_mob {
            width: 22px;
            height: 17px;
            margin-left: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: column;
            display: none;
            cursor: pointer;
            @media (max-width: 680px) {
                display: flex;
            }
            span {
                display: block;
                width: 22px;
                height: 3px;
                background: $green;
                transition: transform ease 0.4s;
            }
            &.open {
                justify-content: center;
                span {
                    &:first-child {
                        transform: rotate(45deg);
                        margin-bottom: -3px;
                        & + span {
                            display: none;
                        }
                    }
                    &:last-child {
                        transform: rotate(-45deg);
                    }
                }
                & + nav {
                    left: 0;
                }
            }
    }
}
.header {
    &_search {
        border-bottom: 2px solid #bec7d5;
        
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width:950px) {
            display: none;
        }
        input {
            font-size: 16px;
            font-family: $obold;
            padding: 14px 0;
            width: 100%;
                background: none;
            @include plhl() {
                color: #8f96a1;
            }
        }
        button {
            font-size: 21px;
            color: #bec7d5;
            background: none;
        }
    }
}
