
.article {
    &s {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: stretch;
        margin-left: -15px;
        img {
            width: 100%;
            display: block;
        }
        & ~ * {
            margin-right: 15px;
            @media (max-width:980px) {
                margin-right: 10px;   
            }
        }
    }
    
    &_item {
        width: calc( 33% - 20px );
        margin-left: 10px;
        margin-right: 10px;
        display: flex;
        flex-direction: column;
        @media (max-width:980px) {
            width: calc( 50% - 20px );
            margin-left: 10px;
            margin-right: 10px;
        }
        @media (max-width:560px) {
            width: 100%;
            margin-left: 15px;
            margin-right: 0px;
        }
        @media (min-width:750px) {
            &_big {
                width: calc( 100% - 20px );
                display: block;
                margin-left: 15px;
                margin-right: 17px;
                @media (max-width:980px) {
                    margin-left: 10px;
                    margin-right: 10px;
                }
                img {
                    width: auto;
                    display: block;
                    height: 100%;
                }
                .article {
                    &_img {
    //                    width: 300px;
                        overflow: hidden;
                    }
                    &_item {
                        &_block {
                            display: flex;
                            align-items: stretch;
                            & > div {
                                padding: 40px 30px;
                            }
                            p {
                                margin-bottom: 16px;
                                font-size: 16px;
                            }
                        }
                        &_head {
                            font-size: 30px; 
                            &:after {
                                width: 40px;
                                margin: 18px 0;
                            }
                        }
                        &_more {
                            padding-left: 0;
                        }
                    }

                }
            }
        }
        
        &_big {
            .article_img {
                @media (max-width:750px) {
                    max-height: 158px;
                    overflow: hidden;
                    display: block;
                }
            }
        }
        &_head {
            font-family: $gbold;
            font-size: 20px;
            line-height: 24px;
            color: #27302d;
            text-decoration: none;
            &:after {
                content: '';
                display: block;
                width: 40px;
                background: $green;
                height: 4px;
                border-radius: 10px;
                margin: 12px 0;
            }
            &:hover {
                color: $green;
            }
        }
        &_block {
            
            & > div {
                color: #8f96a1;
                font-family: $med;
                font-size: 14px;
                padding: 15px 17px;
            }
            p {
                margin-bottom: 10px;
            }
        }
        &_more {
            text-transform: uppercase;
            color: $green;
            font-family: $gbold;
            text-decoration: none;
            font-size: 14px;
            display: block;
            padding: 5px 0 17px 15px;
            i {
                margin-left: 13px;
            }
        }
    }
}

.aside {
    &_article {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start; 
        img {
            border-radius: 5px;
            margin-right: 17px;
        }
        &_name {
            font-size: 14px;
            line-height: 18px;
            color: #27302d;
            font-family: $gbold;
            text-decoration: none;
            display: block;
            &:hover {
                color: $green;
            }
        }
        span {
            font-size: 12px;
            color: #8f96a1;
            font-family: $med;
        }
        i {
            margin-right: 5px;
        }
        & + & {
            margin-top: 16px;
            padding-top: 16px;
            border-top: 1px solid #e6eaf0;
        }
    }
    &_nav {
        color: #fff;
        font-size: 16px;
        font-family: $gbold;
        &_head {
            cursor: pointer;
            span {
                display: inline-block;
                width: 22px;
                height: 22px;
                border: 2px solid #fff;
                border-radius: 50%;
                vertical-align: middle;
                margin-top: -2px;
                margin-right: 15px;
                position: relative;
                &:after,
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 10px;
                    height: 2px;
                    background: #fff;
                    left: 50%;
                    top: 50%;
                    margin: -1px 0 0 -5px;
                    transition: all ease 0.4s;
                }
                &:after {
                    transform: rotate(90deg);
                }
            }
            &.open {
                span {
                   &:after {
                        transform: rotate(0deg);
                    } 
                }
            }
        }
        &_in {
            padding: 15px 0 0px 37px;
            display: none;
            a {
                color: #fff;
                font-family: $gbold;
                font-size: 14px;
                text-decoration: none;
                display: block;
                margin-bottom: 7px;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        &_item {
            & + & {
                margin-top: 17px;
            }
        }
    }
}

.form {
    &_top {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        
        
        padding-bottom: 32px;
        margin-bottom: 32px;
        border-bottom: 1px solid #e6eaf0;
        @media (max-width:550px) {
            padding-bottom: 20px;
            margin-bottom: 20px;
        }
        & + & {
            padding-bottom: 0px;
            margin-bottom: 20px;
            border-bottom: none;
        }
        & > * {
            width: 30%;
        }
        
        
    }
    &_block {
        margin-bottom: 20px;
        b {
            display: block;
            color: #979ea8;
            font-size: 16px;
            margin-bottom: 10px;
            font-family: $gbold;
            font-weight: normal;
        }
        label {
            display: block;
            @media (max-width:950px) and (min-width:550px) {
                &:not(.label_name) {
                    width: 48%;
                }
                &.label_name {
                    width: 100%;
                    input {
                        width: 48%;
                    }
                }
            }
            @media (max-width:550px) {
                width: 100%;
            }
        }
        textarea,
        input {
            padding: 12px 10px;
            border: 1px solid #e6eaf0;
            border-radius: 3px;
            width: 100%;
            font-size: 16px;
        }
        textarea {
            min-height: 80px;
        }
    }
}


