@import "variables";
@import "mixins";
html,
body {
    font-family: $reg;
    background: rgb(255,255,255);
    background: -moz-linear-gradient(top, rgba(255,255,255,1) 0%, rgba(240,242,245,1) 50%);
    background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(240,242,245,1) 50%);
    background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(240,242,245,1) 50%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f0f2f5',GradientType=0 );
    
        background-attachment: fixed;
        height: 100%;

}
.wrapper {
    max-width: 1175px;
    padding: 0 15px;
    margin: 0 auto;
    input {
        &[type="radio"] {
            display: none;
            & + span {
                width: 14px;
                min-width: 14px;
                height: 14px;
                min-height: 14px;
                border: 1px solid #8f96a1;
                border-radius: 50%;
                display: block;
                position: relative;
                margin-right: 10px;
            }
            &:checked {
                & + span {
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        top: 1px;
                        left: 1px;
                        width: 10px;
                        height: 10px;
                        border-radius: 50%;
                        background: $green;
                    }
                }
            }
        }
    }
    label {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        color: #27302d;
        font-family: $med;
        font-size: 14px;
        cursor: pointer;
        margin-bottom: 12px;
    }
    &_page {
        .content_mob {
            .aside_nav {
                display: none!important;
            }
        }
        aside .aside_nav {
            display: block;
        }
    }
}
.block {
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(43, 59, 84, 0.1);
    
    margin-bottom: 16px;
    overflow: hidden;
    
    background: #fff;
    &.green {
        background: $green;
    }
    &_head {
        padding: 15px;
        color: #27302d;
        font-size: 16px;
        line-height: 24px;
        font-family: $gbold;
        border-bottom: 1px solid #e6eaf0;
        &.green {
            background: $green;
            color: #fff;
            border-bottom-color: $green;
        }
    }
    &_content {
        padding: 20px 17px;
    }
}
.content {
    display: flex;
    padding-bottom: 40px;
    @media (max-width:700px) {
        display: block;
    }
    &_block {
        width: 100%;
    }
    &_mob {
        display: none;
        @media (max-width:700px) {
            display: block;
            .header_search {
                margin-bottom: 17px;
                display: flex;
            }
            .aside_nav {
                display: block;
            }
        }
    }
}
h1 {
    font-family: $gbold;
    color: #27302d;
    margin-bottom: 30px;
    
    font-size: 30px;
}
.page {
    padding: 40px;
    margin-right: 15px;
    & ~ * {
            margin-right: 15px;
            @media (max-width:980px) {
                margin-right: 10px;   
            }
        }
    @media (max-width:800px) {
        padding: 25px;
    }
    @media (max-width:700px) {
        margin-right: 0;
        padding: 15px;
    }
}
aside {
    min-width: 275px;
    @media (max-width:850px) {
        min-width: 235px;
    }
    .header_search {
        display: none;
        @media (max-width:950px) and (min-width:700px) {
            display: flex;
            margin-bottom: 16px;
        }
    }
}
@media (max-width:700px) {
    .aside_nav {
        display: none;
    }
}

.btn {
    padding: 13px 23px;
    background: $green;
    border-radius: 5px;
    text-align: center;
    display: inline-block;
    border: none;
    outline: none;
    cursor: pointer;
    border: 1px solid $green;
    color: #fff;
    font-family: $gbold;
    transition: all ease 0.4s;
    text-decoration: none;
    &:hover {
        background: none;
        color: $green;
    }
}

form {
    .btn {
        margin-top: 10px;
    }
}

.bread {
    color: #8f96a1;
    font-family: $gbold;
    margin-bottom: 17px;
    a {
        font-size: 16px;
        color: $green;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}

.main {
    display: flex;
    flex-direction: column;
    height: 100%;
    &_p {
        height: 100%;
    }
}
